import React from 'react';
import { useDispatch } from 'react-redux';
import { useFormik } from 'formik';
import * as Yup from 'yup';

import RegistrationForm from './RegistrationForm';
import { registration } from '../../services/API';
import withAuthRedirect from '../WithAuthRedirect/WithAuthRedirect';
import withConnectByGoogle from '../../hoc/WithConnectByGoogle';

const ContainerRegistrationForm = () => {
  const dispatch = useDispatch();

  const formik = useFormik({
    initialValues: {
      userName: '',
      email: '',
      password: '',
      passwordRepeat: '',
    },
    validationSchema: Yup.object({
      userName: Yup.string()
        .matches(
          /(^[a-zA-Z|a-zA-Z0-9|A-z-]{3,100}$)|([^\s]+(\s.*))/,
          '  ',
        )
        .required('  '),
      email: Yup.string()
        .email('  ')
        .required('  '),
      password: Yup.string()
        .min(6, '     6 ')
        .max(30, '     30 ')
        .matches(/^(?![.]|-)/, '  ') // ? - , ! - 
        .matches(/^\S*$/, '  ')
        .required('  '),
      passwordRepeat: Yup.string()
        .oneOf([Yup.ref('password'), null], '  ')
        .required('  '),
    }),
    // validate,
    onSubmit: values => {
      JSON.stringify(values, null, 4);
      const userRequest = {
        name: {
          fullName: formik.values.userName,
        },
        email: formik.values.email,
        password: formik.values.password,
      };

      dispatch(registration(userRequest));
    },
  });

  return (
    <RegistrationForm
      formik={formik}
      userName={formik.values.userName}
      email={formik.values.email}
      password={formik.values.password}
      passwordRepeat={formik.values.passwordRepeat}
      onChange={formik.handleChange}
      onSubmit={formik.handleSubmit}
      onBlur={formik.handleBlur}
    />
  );
};

export default withConnectByGoogle(withAuthRedirect(ContainerRegistrationForm));
