import React from 'react';
import styles from './Quote.module.css';

const Quote = () => {
  return (
    <div className={styles.wrapperQuote}>
      <p className={styles.quote}>
            ,      
               .
      </p>
      <p className={styles.quoteAuthor}> .</p>
    </div>
  );
};

export default Quote;
