import React from 'react';
import { Redirect, Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import styles from './StartPage.module.css';

const useStyles = makeStyles(theme => ({
  logInButton: {
    border: '1px solid black',
    fontSize: '13px',
    fontWeight: '600',
    width: '45%',
    height: '39px',
    marginLeft: '-4%',
  },
  signInButton: {
    width: '45%',
    height: '39px',
    fontSize: '13px',
    fontWeight: '600',
    color: theme.palette.primary.light,
    '&:hover': {
      backgroundColor: 'var(--orange)',
    },
    backgroundColor: 'rgba(255, 107, 8, 1)',
  },
}));

const StartPage = () => {
  const classes = useStyles();
  const width = document.documentElement.clientWidth;

  return (
    <>
      {width <= 760 ? (
        <div className={styles.wrapperQuote}>
          <h1 className={styles.quoteTitle}>Books Reading</h1>
          <h2 className={styles.quoteSubtitle}> </h2>
          <ul className={styles.helpList}>
            <li className={styles.helpListItem}>
                    
            </li>
            <li className={styles.helpListItem}>
                   
            </li>
            <li className={styles.helpListItem}>
                
            </li>
          </ul>

          <h2 className={styles.quoteSubtitle}>  </h2>

          <ul className={styles.canList}>
            <li className={styles.canListItem}>
                   
            </li>
            <li className={styles.canListItem}>
                     
            </li>
            <li className={styles.canListItem}>
                
            </li>
          </ul>
          <div className={styles.wrapperButton}>
            <Button
              to="/login"
              size="large"
              type="submit"
              variant="outlined"
              className={classes.logInButton}
            >
              <Link to="/login" className={styles.logInLink}>
                
              </Link>
            </Button>

            <Button
              variant="outlined"
              size="large"
              type="submit"
              className={classes.signInButton}
            >
              <Link to="/registration" className={styles.signInLink}>
                
              </Link>
            </Button>
          </div>
        </div>
      ) : (
          <Redirect to="/login" />
        )}
    </>
  );
};

export default StartPage;
